export default {
	data() {
		return {
			userInfo: {}
		}
	},
	onshow() {
		console.log('personal')
		this.userInfo = uni.getStorageSync('userInfo') || {}
	},
	methods: {
		getUserInfo(e) {
			this.userInfo = e.detail.userInfo
			uni.setStorageSync('userInfo', this.userInfo)
		},
		goToPage(url) {
			if (!url) {
				return
			}
			uni.navigateTo({
				url
			})
		},
		clearUserInfo() {
			uni.removeStorageSync('userInfo')
		}
	}
}
